.product {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    margin: 10px;
    padding: 20px;
    width: 100%;
    max-height: 400px;
    min-width: 100px;
    background-color: white;
    z-index: 1;
  

  }
  
  .product__rating {
    display: flex;
  }
  
  .product__img {
    max-height: 200px;
    width: 100%;
    object-fit: contain;
    margin-top: 10px;
    margin-bottom: 15px;
    transition: transform .2s;
  }

  .product__img:hover {
    transform: scale(1.5);
  }
  
 
  .product__price {
    /* margin-top: 5px; */
  }
  
  .product__info {
    height: 100px;
    margin-bottom: 15px;
  }

