.header{
    height: 80px;
    display: flex;
    align-items: center;
    background-color: black;
    position: sticky;
    top: 0;
    z-index: 100;
    
}

.header__logo{
    width: 100px;
    object-fit: contain;
    margin: 0 20px;
    margin-top: 18px;
}

.header__search{
    display: flex;
    flex : 0.95;
    align-items: center;
    border-radius: 24px;
    margin-right: 10px;
}

.header__searchInput{
    /* height: 19px;
    padding: 10px;
    border : none; */
    width: 100%;
}

.header__searchButton{
    height : 40px !important;
}


.header__optionLineOne{
    font-size : 15px;

}


.header__optionLineTwo{
    font-size : 15px;
    font-weight: 800;
    
}

.header__optionBasket{
    margin-left: 10px;
    margin-right: 10px;
    display: flex;
    align-items: center;
    color: white;
}

.header__basketCount{
    margin-left: 10px;
    margin-right: 10px;
}

.header__nav{
    display: flex;
    justify-content: space-evenly;
}

.header__option{
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    margin-right: 10px;
    color: white;
}